body {
  margin: 1vh;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: calc(9px+1vw);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.App-header {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  background-color: #282c34;
  height: 4vh;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: white;
}

a {
  text-decoration: none;
  color: black;
}
.App-header a {
  color: white;
  padding: 1vmin;
  margin: auto, 2vmin;
}
.App-header p {
  padding: 1vmin;
  margin: auto, 2vmin;
}

.Home {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.videoListWithSeach {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.videoList {
  width: 80%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: space-around;
          align-content: space-around;
  -webkit-align-items: space-around;
          align-items: space-around;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.VideoCard {
  width: 248px;
  height: 312px;
  margin-bottom: 1vh;
  border: 0.1em solid black;
  word-wrap: break-word;
  overflow: hidden;
}

.VideoCard img {
  width: 100%;
}

.cardContent {
  margin: 0.5vw;
}
.cardContent p,
.cardContent h5 {
  margin: 0 0 0.3vh 0;
}

.search {
  margin: 3vh 2vh;
}

.Video {
  margin: 3vh;
  width: 420px;
}

.VideoContent {
  margin: 1vh;
}

.VideoContent p {
  text-align: left;
  word-wrap: break-word;
  margin: 0 0 1vh 0;
}

button {
  color: darkred;
  border: 1px outset black;
  padding: 0.5vh 2.5vh;
  margin: 0.5vh;
}
input {
  border: 1px outset black;
  padding: 0.5vh 2.5vh;
  margin: 0.5vh;
}

